import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import { http_methods, urls } from "./urls";
import { transformResponse } from "../utils/transform-response";
import { transform_error_response } from "./databaseAPI";
var HOST_URL = urls.HOST_URL, LOGIN_URL = urls.LOGIN_URL;
var POST = http_methods.POST;
export var signInAPI = createApi({
    reducerPath: "signIn",
    baseQuery: fetchBaseQuery({ baseUrl: HOST_URL }),
    endpoints: function (build) { return ({
        login: build.mutation({
            query: function (body) { return ({
                url: LOGIN_URL,
                method: POST,
                body: body,
            }); },
            transformResponse: (transformResponse),
            transformErrorResponse: transform_error_response,
        }),
    }); },
});
export var useLoginMutation = signInAPI.useLoginMutation;
