import { useEffect } from "react";
import { useSignIn } from "./useSignIn";
import { useAppSnackbar } from "./useAppSnackbar";
import { error_messages, server_messages_RU } from "../utils/text";
import { useAppDispatch, useAppSelector } from "../store";
import { setUser } from "../store/userSlice";
export var useErrorsDisplay = function () {
    var dispatch = useAppDispatch();
    var showSnackbar = useAppSnackbar().showSnackbar;
    var userError = useSignIn().error;
    var dataError = useAppSelector(function (state) { return state.databaseStatusState.error; });
    useEffect(function () {
        if (userError)
            showSnackbar(userError);
        if (dataError)
            showSnackbar(dataError);
        if (dataError === server_messages_RU[error_messages.accessDeny])
            dispatch(setUser({ username: null, token: null }));
    }, [userError, dataError]);
};
