import Cookies from "js-cookie";
export var getCookies = function () {
    return {
        username: getCookie("username") || null,
        token: getCookie("token") || null,
    };
};
export var setCookies = function (cookies) {
    Object.keys(cookies).forEach(function (name) {
        return Cookies.set(name, cookies[name]);
    });
};
export var removeCookies = function () {
    removeCookie("username");
    removeCookie("token");
};
var removeCookie = function (name) {
    Cookies.remove(name);
};
var getCookie = function (name) { return Cookies.get(name); };
